body{ 
    margin: 0; 
    padding: 0;
    font-family: "Open Sans", sans-serif;
}
button { 
    cursor: pointer;
    border: none; 
}

.centered{
    text-align: center;
} 
.column{ 
    display: flex;
    justify-content: space-between;
}
.column button{
    height: 40px;
    margin-top: 12px; 
    background: none;
    font-weight: bold; 
    color: rgb(116, 116, 116);
    border-bottom: 1px solid gray;
    padding: 10px;;
}
.column button:hover{ color: black; }
button.red{
    background-color: darkred; 
    color: white; 
    border-radius: 8px; 
}
button.blue{
    background-color: rgb(0, 119, 255); 
    color: white; 
    border-radius: 8px; 
}
button.green{
    background-color: green;
    color: white; 
    border-radius: 8px; 
}
.header{
    background-color: green;
    height: 48px;
    width: 100%;
    position: fixed;
    top: 0;
    border: 1px solid green; 
    padding-bottom: 5px; 
}  
.header h3{ 
    margin-left: 12px; 
    color: white;
}
h2{
    color: green;
    text-align: center; 
}
.displayList{ 
    width: 100%;
    padding-top: 55px;
    margin-bottom: 75px; 
    border-bottom: 1px solid #d1d1d1;
}
.displayList > div{
    border-top: 1px solid #dddcdc;
    padding: 8px 15px;  
}
.displayList p{    
    margin-bottom: 3px;
    margin-top: 0;
}
.displayList > div > p{
    font-style: italic;
    color: gray;
}
.displayList .mainLine{
    display: flex;
    justify-content: space-between; 
    font-weight: bold;
    color: rgb(77, 77, 77);
}
.displayList .mainLine .add{
    color: green;
}
.displayList .mainLine .subtract{
    color: red;
}
.displayList > .interact{
    border-left: 6px solid rgb(8, 189, 8);
}
.displayList > .interact:nth-child(odd){
    border-left: 6px solid rgb(173, 173, 173);
}
.displayList .interact .column{
    font-style: italic;
    color: rgb(131, 131, 131);
}
.displayList > .interact:hover{
    background-color: #f1f1f1;
    cursor: pointer;
}  
.displayList .info{
    padding: 0;
    background-color: rgb(228, 228, 228); 
    border: 1px solid rgb(228, 228, 228);
} 
.displayList .info p{ 
    color: black;
    font-weight: bold; 
    font-style: normal;
    margin-top: 6px; 
    padding-bottom: 5px;
}
.options{ 
    overflow: hidden;
    display: none;
    padding-top: 18px; 
    margin-top: 12px; 
    margin-bottom: 10px; 
    border-top: 1px solid rgb(211, 211, 211); 
} 
.options button{ 
    padding: 12px 0;
    width: 30%;
    font-weight: bold; 
    border-radius: 8px;
} 
.show .options{ 
    display: block;
    -webkit-animation: slideIn .2s;
            animation: slideIn .2s; 
    display: flex;
    justify-content: space-around;
}
@-webkit-keyframes slideIn{
    from{height: 0px }
    to{ height: 50px; }
}
@keyframes slideIn{
    from{height: 0px }
    to{ height: 50px; }
}
#settingsContainer{
    position: fixed;
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.6); 
    z-index: 1114;
}
#settingsBox{
    position: absolute;
    background-color: white;
    box-shadow: 0 0 12px black;
    height: 90vh;
    margin: 5vh 5%;
    width: 90%;
    z-index: 3;
    border-radius: 10px;
}
#settingsBox button{
    display: block;
    width: 85%;
    padding: 10px; 
    margin: 20px auto;
}
strong, #settingsBox p{ 
    display: block;
    margin-top: 25px; 
    font-weight: bold;
    width: 100%;
    text-align: center;
    font-size: 1.2em;
}
#settingsBox p{
    font-size: 1em;
    color: red;
    width: 90%;
    margin-left: 5%;
}
#settingsBox .close_btn{
    position: absolute;
    top: 0;
    right: 10px; 
    width: 50px;   
    text-align: center; 
}
.actionDrawer{
    position: fixed;
    top: 100vh;
    width: 100%; 
    text-align: center;
    z-index: 1; 
    background: #c4f4bc; /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, #c4f4bc 10%,#e6ffd1 44%,#cff9b8 44%,#e7ffdd 100%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-linear-gradient(315deg, #c4f4bc 10%,#e6ffd1 44%,#cff9b8 44%,#e7ffdd 100%);
    background: linear-gradient(135deg, #c4f4bc 10%,#e6ffd1 44%,#cff9b8 44%,#e7ffdd 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#c4f4bc', endColorstr='#e7ffdd',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
} 
#drawerToggle{
    background-color: green;
    color: white;
    border: none; 
    height: 60px;
    width: 60px;
    font-size: 3em;
    border-radius: 50%;
    -webkit-animation-name: rotateOff;
            animation-name: rotateOff;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    position: relative;
    top: -80px;
}
.actionDrawer.opened{
    bottom: -32px;
    top: auto;
    width: 92%;
    margin: 0 4%;
    border-radius: 5px 5px 0 0;
    box-shadow: 0 0 12px rgb(161, 161, 161);
}
.actionDrawer.opened #drawerToggle{
    background-color: red;
    -webkit-animation-name: rotate;
            animation-name: rotate;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg)
}
.actionDrawer h2{
    background-color: green;
    padding: 8px 0;
    border-radius: 5px 5px 0 0;
    color: white; 
    margin: 0;
} 
.actionDrawer form{
    position: relative;
    top: -60px;
    text-align: left; 
    width: 100%; 
    margin: 0 auto;   
}
input[type=text], input[type=number], input[type=password], select{
    padding: 12px 5px;
    width: 87%;
    margin: 5px 5%;
    border: none;
    font-size: 1em;
    box-shadow: 0 0 3px rgb(83, 192, 83);
}  
select{ width: 88%; } 
#cleared{ font-size: 100px; -webkit-transform: scale(2.5); transform: scale(2.5); border: 1px solid green;}
form button{
    width: 90%;
    border: none; 
    padding: 8px; 
    margin: 0 5%;
    background-color: green;
    color: white;
    font-size: 1em;
    border-radius: 6px; 
}
label{
    width: 90%;
    display: block;
    margin: 16px 5% 4px 5%;
    font-weight: bold;
    color: green;
    clear: both;
}

.f_choice{
    color: gray;
    font-weight: bold; 
    padding: 5px 0 5px 26px; 
    width: 35%;
    text-align: center;
    margin: 23px 0 23px 1%;
    position: relative;
    float: left;
}
.f_choice:hover{ 
    color: black; 
    cursor: pointer;
}
.f_choice.choice{
    color: green;
    border: 1px solid green;
    margin-left: 5%;
}
.f_choice.choice::before{
    position: absolute; 
    left: 0px;
    top: -1px;
    padding: 5px 8px;
    content: "\2714";
    border: 1px solid green;
    background-color: green;
    color: white;
}

@-webkit-keyframes rotate{
    from{ -webkit-transform: rotate(0deg); transform: rotate(0deg); background-color: green; }
    to{ -webkit-transform: rotate(45deg); transform: rotate(45deg); background-color: red; }
}

@keyframes rotate{
    from{ -webkit-transform: rotate(0deg); transform: rotate(0deg); background-color: green; }
    to{ -webkit-transform: rotate(45deg); transform: rotate(45deg); background-color: red; }
}
@-webkit-keyframes rotateOff{
    from{ -webkit-transform: rotate(45deg); transform: rotate(45deg); background-color: red; }
    to{ -webkit-transform: rotate(0deg); transform: rotate(0deg); background-color: green ;}
}
@keyframes rotateOff{
    from{ -webkit-transform: rotate(45deg); transform: rotate(45deg); background-color: red; }
    to{ -webkit-transform: rotate(0deg); transform: rotate(0deg); background-color: green ;}
}

